// import { ref } from 'vue';
import { ref, watch } from 'vue';
import { useStore } from 'vuex'
import { allLanguages } from '@/data/globals'

export default function useLang () {
// export default function useLang (name) {
    // console.log('useLang [name='+name+']')
    const store = useStore()
    const lang = ref(store.state.language)

    function checkUserLanguage() {
        console.log('localStorage getItem')
        let localItem = localStorage.getItem('language')
        if(localItem) localItem = encodeURI(localItem)

        // Для информации:
        // encodeURI заменяет киррилические и другие символы на соответствующими им UTF-8 escape-последовательности. Незаменяемые символы: латинский алфавит, десятичные цифры, - _ . ! ~ * ' ( ) #

        // if(localItem !== 'en' && localItem !== 'uk') localItem = 'uk'
        if(!allLanguages.includes(localItem)) localItem = 'uk'

        // console.log('   localItem: ', localItem);
        // console.log('   1) store.state.language: ', store.state.language);
        store.commit('SetLanguage', localItem)
        console.log('   store.state.language: ', store.state.language);
        // }
        // else {
        //     console.log('    localItem "language" is empty')
        // }

        console.log('-------- [1] --------')
    } // checkUserLanguage



    watch(() => store.state.language, function() {
    // watch(() => store.getters.language, function() {
        lang.value = store.state.language
        // console.log('useLang [name='+name+'] -> lang changes detected ')
    })



    return { checkUserLanguage, lang }
}