// import { onMounted, ref } from 'vue';
import { ref } from 'vue'
import { useStore } from 'vuex'
// import { useRouter } from 'vue-router'
import axios from 'axios'
// import { pathToAPI, global } from '@/data/globals.js'
import { define, welcNotifyDuration } from '@/data/globals.js'
import useNotify from '@/hooks/useNotify'
import notifys from '@/data/languages/notifications/useAuthData'
import useLang from '@/hooks/useLang'

export default function useAuth () {
  const store = useStore()
  // const router = useRouter()
  const { pushNotify } = useNotify()
  const { lang } = useLang('useAuth')
  const email = ref('user@gmail.com')
  const password = ref('888')
  // const email = ref('')
  // const password = ref('')
  // const localToken = ref(localStorage.getItem('AccessToken'))

  let gettedItem = localStorage.getItem('AccessToken')
  if(gettedItem) gettedItem = encodeURI(gettedItem)
  const localToken = ref(gettedItem)
  // Для информации:
  // encodeURI заменяет киррилические и другие символы на соответствующими им UTF-8 escape-последовательности. Незаменяемые символы: латинский алфавит, десятичные цифры, - _ . ! ~ * ' ( ) #
  // console.log('localToken.value.length: ', localToken.value.length);

  async function login() {
    try {
      const response = await axios.post( define.pathToAPI + "/access/login.php", 
        JSON.stringify({
          email: email.value,
          password: password.value,
        }),
        {
          withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true")
          headers: {
            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
            Accept: 'application/json',
            'Content-Type': 'application/json',
          }
        }
      ) // axios

      // console.log('response: ', response)
      // console.log('response.data: ', response.data)
      // console.log('-----')
      console.log('------- login -------', response)
      // console.log('response: ' + response)
      console.log('---------------------')

      if(response.data.status === 'approved' &&
        response.data.accessToken &&
        response.data.name
      ){
        localStorage.setItem('AccessToken', response.data.accessToken)
        localStorage.setItem('name', response.data.name)
        axios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.accessToken

        // Добро пожаловать!<br>Вам открыт доступ к приложению
        pushNotify( notifys[lang.value].welcome, "notifyWelcome", welcNotifyDuration)
        notifys[lang.value]

        store.commit('SetIsAuth', true)
        // location.reload()
        // router.push('/')
      } else {
        // Ошибка. Сервер вернул некорректный ответ
        pushNotify( notifys[lang.value].incorrectResp, 'notifyOrange')
      }

    } catch (e) {
      // console.log('e: ', e)

      if(e.code === 'ERR_NETWORK') {
        // Нет подключения к серверу
        pushNotify( notifys[lang.value].errNetwork, 'notifyOrange')
        console.log('Нет подключения к серверу')
        return
      }

      // Обработка ошибок, когда front и back на разных хостах (CORS):
      switch(e.response.data.status) {
        case 'expired':
          tokenRefresh()
          break
        case 'denied':
          // Доступ закрыт<br>Проверьте логин и пароль
          pushNotify( notifys[lang.value].text1, 'notifyOrange')
          break
        default:
          // Ошибка. Подробности в консоли
          pushNotify( notifys[lang.value].someError, 'notifyOrange')
          console.log(e)
          break
      } // switch

      // // Обработка ошибок, когда front и back на одном хосте (НЕ CORS):
      // switch(e.response.status) {
      //   case 0:
      //     console.log('Нет соединения с сервером')
      //     console.log('Сделать пуш уведомление')
      //     break
      //   case 401:
      //     console.log('Логин или пароль указаны неверно / ' + e.response.data.message)
      //     console.log('Сделать пуш уведомление')
      //     break
      //   default:
      //     console.log(e)
      //     console.log('Сделать пуш уведомление')
      //     break
      // } // switch
    }// try
  
  } // login

  async function logout() {
    try {
      const response = await axios.post( define.pathToAPI + "/access/logout.php", 
        JSON.stringify({
          // test: 'Test_Value',
          message: 'Привет сервер! С этим сообщением ты сможешь прочитать cookie', // отправляем любые данные (любое поле), если хотим читать cookie на беке при CORS
          // Важно! Для информации:
          // Cookie передаются в заголоке POST запроса.
          // Однако, при CORS, делаются не 1 а 2 запроса (OPTIONS и за ним POST).
          // Причем, если в axios мы не добавим хотя бы одно поле данных (отправим только заголовки), то в таком случае будет отправлен только 1 запрос OPTIONS. А, значит, на беке мы не сможем прочитать cookie. Их нет в OPTIONS, а второй запрос (POST) с cookie, не будет передан!
        }),
        {
          withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true");
          headers: {
            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
            Accept: 'application/json',
            'Content-Type': 'application/json',
          }
        }
      ) // axios

      console.log('----- logout -----', response)
      // console.log('response: ' + response)
      console.log('------------------')


      if(response.data.status === 'ok'){
        // delete axios.defaults.headers.common['Authorization']
        // store.commit('SetIsAuth', false)
        // localStorage.removeItem('AccessToken')
        // localStorage.removeItem('name')
        removeAccessData()
        // location.reload()
        // router.go(0)
        // Вы вышли из приложения
        pushNotify( notifys[lang.value].bye, "notifyWelcome", welcNotifyDuration)
      } else {
        // Ошибка. Ответ сервера: ...<br>Подробности в консоли
        pushNotify( notifys[lang.value].text5_1 + response.data.error + notifys[lang.value].text5_2, 'notifyOrange')
        console.log(response)
      }

    } catch (e) {
      
      if(e.code === 'ERR_NETWORK') {
        // Нет подключения к серверу
        pushNotify( notifys[lang.value].errNetwork, 'notifyOrange')
        console.log('Нет подключения к серверу')
        return
      }

    } finally {
      // store.commit('SetIsAuth', false)
      // delete axios.defaults.headers.common['Authorization']
      // localStorage.removeItem('AccessToken')
      // localStorage.removeItem('name')
      // location.reload()
      // // this.$router.push('/login')
      // pushNotify("Вы вышли из приложения", "notifyWelcome", welcNotifyDuration)
    } // finally

  } // logout

  function removeAccessData() {
    delete axios.defaults.headers.common['Authorization']
    store.commit('SetIsAuth', false)
    localStorage.removeItem('AccessToken')
    localStorage.removeItem('name')
  } // removeAccessData

  async function validateToken() {
    // console.log('\nvalidateToken!\n');
    // console.log('localToken.value: ', localToken.value);

    if(!localToken.value) return
    axios.defaults.headers.common['Authorization'] = 'Bearer ' + localToken.value

    try {
      const response = await axios.post( define.pathToAPI + "/access/validate_token.php", 
        // JSON.stringify({
        //   // test: 'Test_Value',
        //   message: 'Привет сервер! С этим сообщением ты сможешь прочитать cookie', // отправляем любые данные (любое поле), если хотим читать cookie на беке при CORS
        //   // Важно! Для информации:
        //   // Cookie передаются в заголоке POST запроса.
        //   // Однако, при CORS, делаются не 1 а 2 запроса (OPTIONS и за ним POST).
        //   // Причем, если в axios мы не добавим хотя бы одно поле данных (отправим только заголовки), то в таком случае будет отправлен только 1 запрос OPTIONS. А, значит, на беке мы не сможем прочитать cookie. Их нет в OPTIONS, а второй запрос (POST) с cookie, не будет передан!
        // }),
        {
          withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true");
          headers: {
            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
            Accept: 'application/json',
            'Content-Type': 'application/json',
          }
        }
      ) // axios

      console.log('----- validateToken -----', response)
      // console.log('response: ' + response)
      console.log('-------------------------')
      
      if(response.data.status === 'allowed'){
        // У вас есть доступ к приложению
        pushNotify( notifys[lang.value].hiAgain, "notifyWelcome", welcNotifyDuration)
        store.commit('SetIsAuth', true)
        // localStorage.setItem('AccessToken', response.data.accessToken)
        // localStorage.setItem('name', response.data.name)
        // axios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.accessToken
      } else {
        // Ошибка. Ответ сервера: ...<br>Подробности в консоли
        pushNotify( notifys[lang.value].text5_1 + response.data.error + notifys[lang.value].text5_2, 'notifyOrange')
        console.log(response)
      }
      // // Обработка ответа
      // switch(response.data.status) {
      //   case 'expired':
      //     pushNotify("Токен просрочен. Сделать Refresh', 'notifyOrange')
      //     break
      //   case 'denied':
      //     pushNotify("Доступ закрыт', 'notifyOrange')
      //     break
      //   default:
      // // Ошибка. Ответ сервера: ...<br>Подробности в консоли
      // pushNotify( notifys[lang.value].text5_1 + response.data.error + notifys[lang.value].text5_2, 'notifyOrange')
      //     console.log(response.data.error)
      //     break
      // } // switch

    } catch (e) {
      // console.log('e: ', e)
      // // Ошибка. Подробности в консоли
      // pushNotify( notifys[lang.value].someError, 'notifyOrange')
      // // Обработка ошибок, когда front и back на разных хостах (CORS):
      // console.log('Доступ ограничен. Проверьте логин и пароль.\nВозникли сложности со входом? Восстановите пароль или напишите в поддержку')
      // pushNotify("Доступ ограничен<br>Проверьте логин и пароль", 'notifyOrange')

      if(e.code === 'ERR_NETWORK') {
        // Нет подключения к серверу
        pushNotify( notifys[lang.value].errNetwork, 'notifyOrange')
        console.log('Нет подключения к серверу')
        return
      }

      // Обработка ошибок, когда front и back на разных хостах (CORS):
      switch(e.response.data.status) {
        case 'expired':
          tokenRefresh()
          break
        case 'failed':
          // Alarm! Передан некорректный токен.<br>Доступ закрыт
          pushNotify( notifys[lang.value].incorrectToken, 'notifyRed')
          break
        case 'denied':
          // Alarm! Передан пустой токен.<br>Доступ закрыт
          pushNotify( notifys[lang.value].emptyToken, 'notifyRed')
          break
        default:
          // Ошибка. Подробности в консоли
          pushNotify( notifys[lang.value].someError, 'notifyOrange')
          console.log(e)
          break
      } // switch

      // // Обработка ошибок, когда front и back на одном хосте (НЕ CORS):
      // switch(e.response.status) {
      //   case 0:
      //     console.log('Нет соединения с сервером')
      //     console.log('Сделать пуш уведомление')
      //     break
      //   case 401:
      //     console.log('Логин или пароль указаны неверно / ' + e.response.data.message)
      //     console.log('Сделать пуш уведомление')
      //     break
      //   default:
      //     console.log(e)
      //     console.log('Сделать пуш уведомление')
      //     break
      // } // switch
    }// try

  } // validateToken



  async function tokenRefresh() {
    console.log('\ntokenRefresh!\n-----------------')
    // if(!localToken.value) return
    // axios.defaults.headers.common['Authorization'] = 'Bearer ' + localToken.value

    try {
      const response = await axios.post( define.pathToAPI + "/access/refresh_token.php", 
        JSON.stringify({
          // test: 'Test_Value',
          message: 'Привет сервер! С этим сообщением ты сможешь прочитать cookie', // отправляем любые данные (любое поле), если хотим читать cookie на беке при CORS
          // Важно! Для информации:
          // Cookie передаются в заголоке POST запроса.
          // Однако, при CORS, делаются не 1 а 2 запроса (OPTIONS и за ним POST).
          // Причем, если в axios мы не добавим хотя бы одно поле данных (отправим только заголовки), то в таком случае будет отправлен только 1 запрос OPTIONS. А, значит, на беке мы не сможем прочитать cookie. Их нет в OPTIONS, а второй запрос (POST) с cookie, не будет передан!
        }),
        {
          withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true");
          headers: {
            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
            Accept: 'application/json',
            'Content-Type': 'application/json',
          }
        }
      ) // axios

      // console.log('----- [1] ----- (tokenRefresh)')
      console.log('response: ', response)
      // console.log('response.data: ', response.data)
      // console.log('response.data.status: ', response.data.status)

      if(response.data.status === 'approved' &&
        response.data.accessToken
      ){
      // if(response.data.status === 'allowed'){
        // Access Token обновлен<br>У вас есть доступ к приложению
        pushNotify( notifys[lang.value].accTokenUpdated, "notifyWelcome", welcNotifyDuration)
        store.commit('SetIsAuth', true)
        localStorage.setItem('AccessToken', response.data.accessToken)
        axios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.accessToken
        return true
      } else {
        // Ошибка. Ответ сервера: ...<br>Подробности в консоли
        pushNotify( notifys[lang.value].text5_1 + response.data.error + notifys[lang.value].text5_2, 'notifyOrange')
        console.log(response)
        return
      }
      // // Обработка ответа
      // switch(response.data.status) {
      //   case 'expired':
      //     pushNotify("Токен просрочен. Сделать Refresh", 'notifyOrange')
      //     break
      //   case 'denied':
      //     pushNotify("Доступ закрыт", 'notifyOrange')
      //     break
      //   default:
      // // Ошибка. Ответ сервера: ...<br>Подробности в консоли
      // pushNotify( notifys[lang.value].text5_1 + response.data.error + notifys[lang.value].text5_2, 'notifyOrange')
      //     console.log(response.data.error)
      //     break
      // } // switch

    } catch (e) {
      // console.log('e: ', e)
      // // Ошибка. Подробности в консоли
      // pushNotify( notifys[lang.value].someError, 'notifyOrange')
      // // Обработка ошибок, когда front и back на разных хостах (CORS):
      // console.log('Доступ ограничен. Проверьте логин и пароль.\nВозникли сложности со входом? Восстановите пароль или напишите в поддержку')
      // pushNotify("Доступ ограничен<br>Проверьте логин и пароль", 'notifyOrange')
      
      if(e.code === 'ERR_NETWORK') {
        // Нет подключения к серверу
        pushNotify( notifys[lang.value].errNetwork, 'notifyOrange')
        console.log('Нет подключения к серверу')
        return
      }

      // Обработка ошибок, когда front и back на разных хостах (CORS):
      switch(e.response.data.status) {
        case 'expired':
          // Залогиньтесь
          pushNotify( notifys[lang.value].loginPlease, 'notifyBlue')
                                        console.log(e)
          // delete axios.defaults.headers.common['Authorization']
          // store.commit('SetIsAuth', false)
          // localStorage.removeItem('AccessToken')
          // localStorage.removeItem('name')
          removeAccessData()
          return
          // break
        case 'failed':
          // Alarm! Передан некорректный токен.<br>Доступ закрыт
          pushNotify( notifys[lang.value].incorrectToken, 'notifyRed')
                                        console.log(e)
          return
          // break
        case 'denied':
          // Залогиньтесь
          pushNotify( notifys[lang.value].loginPlease, 'notifyBlue')
                                    console.log(e)
          // delete axios.defaults.headers.common['Authorization']
          // store.commit('SetIsAuth', false)
          // localStorage.removeItem('AccessToken')
          // localStorage.removeItem('name')
          removeAccessData()
          return
          // break
        case 'locked':
          // Ошибка безопастности<br>Похоже на подделку токена доступа<br>Залогиньтесь или обратитесь в поддержку
          pushNotify( notifys[lang.value].fakeToken, 'notifyBlue', welcNotifyDuration)
                                    console.log('Service is locked')
          // delete axios.defaults.headers.common['Authorization']
          // store.commit('SetIsAuth', false)
          // localStorage.removeItem('AccessToken')
          // localStorage.removeItem('name')
          removeAccessData()
          return
          // break
        default:
          // Ошибка. Подробности в консоли
          pushNotify( notifys[lang.value].someError, 'notifyOrange')
          console.log(e)
          return
          // break
      } // switch

      // console.log('------------- (tokenRefresh)')

      // // Обработка ошибок, когда front и back на одном хосте (НЕ CORS):
      // switch(e.response.status) {
      //   case 0:
      //     console.log('Нет соединения с сервером')
      //     console.log('Сделать пуш уведомление')
      //     break
      //   case 401:
      //     console.log('Логин или пароль указаны неверно / ' + e.response.data.message)
      //     console.log('Сделать пуш уведомление')
      //     break
      //   default:
      //     console.log(e)
      //     console.log('Сделать пуш уведомление')
      //     break
      // } // switch
    }// try
  
  } // tokenRefresh

  


  // onMounted(addLike)
  return { email, password, login, logout, validateToken, tokenRefresh }

}