    async function getTranslations() {
        if(!store.state.isAuth) return
        if(!store.state.updProd.id) return
            console.log('getTranslations() store isAuth: ', store.state.isAuth);

        switch(store.state.language) {
            case allLanguages[0]:
                translateTo.value = allLanguages[1]
                break
            case allLanguages[1]:
                translateTo.value = allLanguages[0]
                break
            default:
                translateTo.value = allLanguages[1]
                break
        }
        // if(store.state.language == 'en' && localItem !== 'uk') localItem = 'uk'
        // if(!allLanguages.includes(localItem)) localItem = 'uk'

        console.log('getTranslations store.state.language: ', store.state.language);
        console.log('translateTo.value: ', translateTo.value);

        try {
            // this.isProdsLoading = true
            const response = await axios.get( define.pathToAPI + "/products/", 
                {
                    params: {
                        language: translateTo.value,
                        // _limit: 100,
                        id: store.state.updProd.id,
                    },
                    // headers: {
                    //     Accept: 'application/json', 'Content-Type': 'application/json',
                    // }
                }
            ) // axios

            console.log('getTranslations response: ', response)
            
            // translation.value = [ response.data.records.brand , response.data.records.model ]

            if(response.data.records[0].id > 0 && response.data.records[0].id == store.state.updProd.id){

                store.commit('SetUpdProdTranslation', [
                    response.data.records[0].brand,
                    response.data.records[0].model
                ])

                // console.log('response.data.records.brand: ', response.data.records[0].brand);
                // console.log('response.data.records.model: ', response.data.records[0].model);
                // location.reload()
                // router.push('/')
            } else {
                // Ошибка. Сервер вернул некорректный ответ
                pushNotify( notifys[lang.value].incorrectResp, 'notifyOrange')
            }

        } catch (e) {
            // console.log('e: ', e);
                
            if(e.code === 'ERR_NETWORK') {
                // Нет подключения к серверу
                pushNotify( notifys[lang.value].errNetwork, 'notifyOrange')
                console.log('Нет подключения к серверу')
                return
            }

            // Обработка ошибок
            switch(e.response.data.status) {
                case 'notfound':
                    // console.log(' notfound ');
                    // this.tokenRefresh()
                    break
                default:
                    // pushNotify('Ошибка. Подробности в консоли', 'notifyOrange')
                    // console.log(e)
                    console.log('error')
                    console.log('e: ', e);
                    break
            } // switch

            // pushNotify('----Ошибка при получении данных от сервера', 'notifyOrange')
            // console.log('----Ошибка при получении данных от сервера\n' + e);

        } finally {
            // this.isProdsLoading = false
        }
        

    } // getTranslations




    
